.Signup {
  .Acrd {}

  .Acrd__cont-c {
    transition: max-height $easing-duration-standard cubic-bezier( $easing-bezier-standard );
    max-height: 0;
    overflow: hidden; }

  .Acrd__cont {
    padding: 1rem 0.75rem 0; }

  .Acrd__trg {
    position: relative;
    height: $height-form-input;
    width: 100%;
    background-color: $grey-very-light;
    border: 1px solid $grey-light;
    border-radius: $border-radius;
    text-align: left;
    padding-left: 1rem;
    cursor: pointer;
    -webkit-appearance: none;

    span {
      color: $grey;
      font-style: italic;
      text-transform: initial;
      margin-bottom: 0;
      font-weight: 300; }

    &::after {
      position: absolute;
      content: '';
      width: 0.65rem;
      height: 0.65rem;
      right: 0.75rem;
      top: 0;
      bottom: 0;
      margin: auto;
      display: block;
      color: $grey;
      background: center / contain no-repeat;
      transition: transform $easing-duration-standard cubic-bezier( $easing-bezier-standard );
      transform: rotate(-45deg) translate(2px, -2px);
      border-left: 2px solid;
      border-bottom: 2px solid; } }

  .Acrd--o {
    .Acrd__cont-c {
        max-height: 400px; }

    .Acrd__trg::after {
      transform: rotate(135deg) translate(2px, -2px); } } }



