.Signup {
  .B {
    display: inline-block;
    font-weight: 400;
    line-height: 1.25;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    cursor: pointer;
    border: none;
    border-radius: $border-radius;
    color: $text-normal;
    background-color: #e6e6e6;
    padding: .5rem 1rem;
    font-size: 1rem;
    transition: all $easing-duration-standard cubic-bezier( $easing-bezier-standard );

    &:hover, &:focus, &:active {
      color: inherit;
      background-color: $grey-light; } }

  .B--p {
    color: $white;
    background-color: $green;
    &:hover, &:focus, &:active {
      color: $white;
      background-color: $green-dark; } } }



