// Fluid Type Mixin

@function strip-unit($value) {
  @return $value / ($value * 0 + 1); }


@mixin fluid-units($attribute, $min-vw, $max-vw, $min-size, $max-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-size);
  $u4: unit($max-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      #{$attribute}: $min-size;
      @media screen and (min-width: $min-vw) {
        #{$attribute}: calc(#{$min-size} + #{strip-unit($max-size - $min-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)})); }

      @media screen and (min-width: $max-vw) {
        #{$attribute}: $max-size; } } }


  @else {
    @error "Detected mixed units. Please use the same units for all parameters. " + $u1 +", " + $u2 + ", " + $u3 +", "+ $u4; } }

// Modular scale basis

$mod_1: 1.067;
$mod_2: 1.25;

$h4-min: $mod_1 * 1rem;
$h4-max: $mod_2 * 1rem;

$h5-min: 1rem;
$h5-max: 1rem;

$h6-min: (1rem / $mod_2);
$h6-max: (1rem / $mod_1);


// Apply fluid type mixin
// @mixin fluid-units($min-vw, $max-vw, $min-font-size, $max-font-size)
// h1, .h1
//   @include fluid-units('font-size', 37.5rem, 80rem, $h1-min, $h1-max)
//   @include fluid-units('margin-bottom', 37.5rem, 80rem, $h3-min, $h3-max)


// Utility Mixins  ============================================================
// ============================================================================

@mixin flex-centered {
  display: flex;
  justify-content: center;
  align-items: center; }

@mixin abs-centered {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto; }



