.Signup {
  margin: 0 auto;
  padding: 0;
  width: 560px;
  max-width: 100%;
  max-width: calc(100% - 1rem);
  font-family: $font-sans;
  font-size: 16px;
  color: $black;
  * {
    box-sizing: border-box; }
  ul {
    list-style: none; }

  .h4 {
    @include fluid-units('font-size', 37.5rem, 80rem, $h4-min, $h4-max);
    @include fluid-units('margin-bottom', 37.5rem, 80rem, $h6-min, $h6-max); }

  span,
  input,
  .F__i {
    margin: 0;
    font-weight: normal;
    @include fluid-units('font-size', 37.5rem, 80rem, 0.875rem, 1rem); } }

.Signup--custom-order.Signup--custom-order {
  * {
    order: initial; } }

.Signup__success {
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  @include flex-centered;
  background-color: rgba(255,255,255,0.75);
  transition: opacity $easing-duration-standard cubic-bezier( $easing-bezier-standard );
  transition-delay: 0.1s; }

.Signup__success--txt {
  width: 100%;
  text-align: center; }

.Signup__success--vis {
  opacity: 1;
  pointer-events: initial; }

.react-datepicker-wrapper.react-datepicker-wrapper,
.react-datepicker__input-container.react-datepicker__input-container {
  display: block; }

.S--condensed {
  @media screen and ($min-width-smedium) {
    .F__g--r {} } }



