.Signup {
  .F,
  .F__E {
    display: flex;
    width: 100%;
    margin: 0 auto; }

  .F {
    flex-flow: row wrap; }

  .F__E {
    flex-direction: column;
    align-items: center;
    justify-content: center; }

  // group
  .F__g {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    flex: 1 0 100%;
    min-width: 100%;
    max-width: 100%; }

  // // group--row
  // .F__g--r
  //   margin-bottom: 0
  //   flex-direction: row

  //   // group within a row
  //   .F__g
  //     flex: 1
  //     &:nth-child(n + 2)
  //       margin-left: 1rem

  // group for checkbox
  .F__g--c {
    flex-direction: row;
    align-items: center; }

  // Account Form controls
  .F__g--AF-c {
    position: absolute;
    top: 0.8rem;
    right: 0.8rem;
    .B {
      margin-left: 0.75rem; } }

  .F__g--message {
    order: 99; }
  .F__g--submit {
    order: 100; }

  // label
  .F__l {
    cursor: pointer;
    display: inline-block;
    @include fluid-units('font-size', 37.5rem, 80rem, $h6-min, $h6-max);
    margin-bottom: 0.25rem;
    font-weight: 300; }

  .F__l--req {
    span {
      display: inline-block;
      position: relative;
      &::after {
        display: block;
        content: '*';
        position: absolute;
        top: 0;
        right: -0.75rem;
        font-size: inherit;
        color: $red; } } }

  // input
  .F__i {
    display: block;
    width: 100%;
    height: $height-form-input;
    padding: .5rem .75rem;
    margin: 0;
    line-height: 1.25;
    font-family: $font-sans;
    color: $text-normal;
    background-color: #fff;
    background-image: none;
    background-clip: padding-box;
    border: 1px solid $grey-light;
    border-radius: $border-radius;
    transition: border-color $easing-duration-standard cubic-bezier( $easing-bezier-standard );
    &:focus, &:active {
      color: #464a4c;
      background-color: #fff;
      border-color: #5cb3fd;
      outline: 0; }
    // fixes horrifying yellow background in chrome autofill
    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus,
    &:-webkit-autofill:active {
      transition: background-color 5000s ease-in-out 0s!important; } }

  .F__i--err {
    border-color: $red;
    &:focus, &:active {
      border-color: $red; } }

  // textarea
  .F__i--t {
    min-height: 130px; }

  // input select
  .F__i--s {
    background: $white calc(100% - 0.75rem)/9px no-repeat;
    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' width='32' height='24' viewBox='0 0 32 24'><polygon points='0,0 32,0 16,24' style='fill: rgb%28138, 138, 138%29'></polygon></svg>");
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    text-indent: 0.01px;
    text-overflow: ellipsis; }

  // input date
  .F__i--d {
    font-family: $font-sans; }

  // input checkbox
  .F__i--c {
    width: 1.5rem;
    height: 1.5rem;

    & ~ .F__l {
      margin-bottom: 0; } }

  .F__sb {
    width: 40%;
    min-width: 120px;
    max-width: 180px;
    margin: 0 auto; }

  // form error notification
  .F__E {
    height: 6rem;
    max-height: 0;
    overflow: hidden;
    border-radius: 2px;
    color: $red;
    background: transparent;
    border: 1px solid transparent;
    padding: 0 1rem;
    margin-bottom: 1rem;
    transition: all $easing-duration-standard cubic-bezier( $easing-bezier-standard );
    p {
      margin: 0; }
    p:nth-child(n + 2) {
      margin-top: 0.5rem; } }

  .F__E--active {
    background: $red-bg;
    border-color: $red; }

  .F__E--1 {
    max-height: 2.5rem; }

  .F__E--2 {
    max-height: 4.5rem; }

  .F__E--3 {
    max-height: 6.5rem; }

  // unset inputs adopt placeholder styles
  @mixin unset-styles {
    font-weight: 300;
    color: $grey-light; }

  .F__i--is-unset {
    @include unset-styles; }
   ::-webkit-input-placeholder {
    @include unset-styles; }
   ::-moz-placeholder {
    @include unset-styles; }
   :-ms-input-placeholder {
    @include unset-styles; }
   :-moz-placeholder {
    @include unset-styles; }


  // custom layout stuff

  // $type-width: 60 // 60%
  // .F__g--type,
  // .F__g--date
  //   display: inline-flex
  //   width: 100%

  // .F__g--type
  //   max-width: calc(#{$type-width}% - 0.5rem)
  // .F__g--date
  //   max-width: calc(#{100 - $type-width}% - 0.5rem)
 }  //   margin-left: 1rem




